.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  width: 1280px;
  /* width: 1360px; */
  align-items: center;
  margin-top: 32px;
  margin-bottom: 28px;
}

@media screen and (max-width: 650px) {
  .header {
    max-width: 600px;
  }
}

@media screen and (max-width: 400px) {
  .header {
    max-width: 350px;
  }
}

.header__leftSideContainer {
  display: flex;
}

.header__dateCity {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  font-size: 20px;
  color: #000;
  margin-left: 24px;
  height: 24px;
  width: 360px;
}

.header__logo {
  height: 20px;
  width: 89px;
  margin-top: 18px;
}

.navigation__container {
  display: flex;
  align-items: center;
}

.navigation__button {
  height: 24px;
  width: 138px;
  border: none;
  background-color: transparent;
  font-weight: 700;
  font-size: 20px;
  color: #000;
  font-family: "Montserrat", sans-serif;
  padding: 0px;
  text-align: right;
  margin-left: 20px;
}

.navigation__signUp {
  font-family: "Montserrat", sans-serif;
  height: 25px;
  width: 187px;
  font-size: 20px;
  font-weight: 700;
  color: #000;
  text-align: right;
  margin-right: 12px;
}

.navigation__signIn {
  font-family: "Montserrat", sans-serif;
  height: 25px;
  width: 187px;
  font-size: 20px;
  font-weight: 700;
  color: #000;
  text-align: right;
  margin-right: 12px;
}

.header__name {
  width: 80px;
  margin-left: 20px;
  font-size: 20px;
  font-weight: 700;
  color: #000;
  text-align: right;
  /* text-decoration: none; */
}

.header__avatar {
  background-image: url("../../images/Ellipse.jpg");
  height: 40px;
  width: 40px;
  border-radius: 50%;
  margin-right: 40px;
  margin-left: 12px;
}

.header__avatarInitial {
  /* position: absolute; */
  background-color: deeppink;
  color: antiquewhite;
  text-align: center;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  margin-left: 12px;
}

.header__profileLink {
  text-decoration: none;
}
